import React, { Component } from 'react';
import './SharedStyles.css';
import './Skills.css';
import Aboutme from './Aboutme';

let randomColoursArr = ["#ce521d", "#ca4b89", "#006b89", "#3e2d7e", "#61902c",
  "#faa31a", "#6e002a", "#4981b3", "#980069", "#2dacbf", "#ee1d25", "#9cb46f",
  "#9a869e", "#ee008c", "#00a895", "#7b181a", "#ffd63c", "#b46638", "#bcd634",
  "#f4ea00", "#32b6c0", "#e8ac1c", "#ea2d50", "#3c7022", "#0085cc", "#97C83B"
  /**/
];

class Skills extends Component {
  render() {

    const skill = ['HTML5', 'CSS3', 'Bootstrap', 'WordPress', 'JavaScript',
      'PHP', 'React.js', 'BEM', 'Photoshop', 'mySQL'
    ];

    const skillsList = skill.map((each, index) => {
      const style = {
        backgroundColor: randomColoursArr[index % randomColoursArr.length]
      };

      return (
        <li key={each} className="skills__list-item" style={style}>
          <a className="skills__list-link">
            {each}
          </a>
        </li>
      );
    });

    return (
      <div className="skills background--fixed">
        <Aboutme />
        <h2 className="heading heading--margin" id="Skills">
          {this.props.heading}
        </h2>
        <ul className="skills__list">
          {skillsList}
        </ul>
      </div>
    );
  }
}

export default Skills;


