import React, { Component } from 'react';
import './Navigation.css';

class Navigation extends Component {
    render() {
        const sections = ['Home', 'Skills', 'Portfolio', 'Contact'];
        const menuLinks = sections.map(section => {
            return <li className="menu__list-item active" key={section}><a className="menu__link" href={'#' + section} name={'#' + section}>{section}</a></li>
        });
        return (
        <nav className="menu menu-position">
                <h2 className="menu__heading">{this.props.title}</h2>
                <p className = "menu__invisible-text "> Front End Developer </p>
            <ul>
              {menuLinks}
                </ul>
            </nav>

    );
  }
}

export default Navigation;




